<template>
    <v-page>
        <div slot="header">
            <v-header>
                <v-btn></v-btn>
                <h1>收款人名册</h1>
                <v-btn-text>
                    <span class="header-btn-txt">手机通讯录</span>
                </v-btn-text>
            </v-header>
        </div>
        <div class="rec-container">
            <v-scroller :outTop="true">
                <ul class="register-list font-num">
                    <li class="item" v-for="(item, index) in registerList" :key="index">
                        <div class="hasMoreCard" v-show="item.cardList.length > 1">
                            <div class="info-wrap" @click="showCardList(index)">
                                <div class="left">
                                    <v-symbol name="card-active-copy"></v-symbol>
                                </div>
                                <div class="middle">
                                    <span class="top">{{item.name}}</span>
                                </div>
                                <div class="right">
                                    <p class="card-count">
                                        <span>{{item.cardList.length}}</span><span>张</span>
                                    </p>
                                    <div class="icon-wrap">
                                        <i class="icon icon-list_icon_down-" :class="{ active: item.showCardList }"></i>
                                    </div>
                                </div>
                            </div>
                            <ul class="card-list" :class="{ active: item.showCardList }">
                                <li class="card-item" v-for="(cardItem, cardIndex) in item.cardList" :key="cardIndex" @click="chooseCard(item.name, cardItem)">
                                    <div class="icon-wrap">
                                        <v-symbol :name="cardItem.bankIcon"></v-symbol>
                                    </div>
                                    <p class="bank-name">{{cardItem.bankName}}</p>
                                    <p class="card-no">{{cardItem.cardNo | formatBankCard}}</p>
                                </li>
                            </ul>
                        </div>
                        <div class="hasOneCard" v-show="item.cardList.length === 1">
                            <div class="info-wrap" @click="chooseCard(item.name, item.cardList['0'])">
                                <div class="left">
                                    <v-symbol :name="item.cardList['0'].bankIcon"></v-symbol>
                                </div>
                                <div class="middle">
                                    <span class="top">{{item.name}}</span>
                                    <p class="bottom">
                                        <span class="bank-name">{{item.cardList['0'].bankName}}</span>
                                        <span class="card-no">{{item.cardList['0'].cardNo | formatBankCard}}</span>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </li>
                </ul>
            </v-scroller>
        </div>
    </v-page>
</template>

<script>
    export default {
        name: 'receiverRegister',
        data () {
            return {
                registerList: [
                    {
                        name: '冯宝宝',
                        showCardList: false,
                        cardList: [
                            {
                                bankIcon: 'zhongguogongshangyinhang',
                                bankName: '中国工商银行',
                                cardNo: '62245454512745454'
                            },
                            {
                                bankIcon: 'zhongguominshengyinhang',
                                bankName: '中国民生银行',
                                cardNo: '6224545451274567'
                            },
                            {
                                bankIcon: 'pufayinhang',
                                bankName: '浦发银行',
                                cardNo: '62245454512741234'
                            }
                        ]
                    },
                    {
                        name: '王也',
                        showCardList: false,
                        cardList: [
                            {
                                bankIcon: 'guangfayinhang',
                                bankName: '广发银行',
                                cardNo: '62245454512741235'
                            }
                        ]
                    },
                    {
                        name: '张之维',
                        showCardList: false,
                        cardList: [
                            {
                                bankIcon: 'zhaoshangyinhang',
                                bankName: '中国招商银行',
                                cardNo: '62245454512741236'
                            }
                        ]
                    }
                ]
            }
        },
        filters: {
            // 格式化银行卡号
            formatBankCard (value) {
                if (!value) return ''
                value = value.toString()
                let reg = /^(\d{4})(.*)(\d{4})$/
                value = value.replace(reg, (match, p1, p2, p3) => {
                    return p1 + ' **** ' + p3
                })
                return value
            }
        },
        methods: {
            // 显示折叠的卡
            showCardList (index) {
                this.registerList[index].showCardList = !this.registerList[index].showCardList
            },
            // 选择卡片
            chooseCard (name, cardInfo) {
                cardInfo.name = name
                let jumpBackUrl = 'main/remittance/remittanceInput'
                let obj = {
                    recName: cardInfo.name,
                    recCardNo: cardInfo.cardNo
                }
                this.jumpBack(jumpBackUrl, obj)
            },
            // 返回页面
            jumpBack (jumpBackUrl, params) {
                console.log(Object.assign(this.remittanceInfo, params))
                window.T.back({
                    params: Object.assign(this.remittanceInfo, params),
                    back: jumpBackUrl
                })
            },
            // 获取页面传递参数
            getPageParams () {
                window.T.Native.getPageParams().then(({params}) => {
                    this.remittanceInfo = Object.assign(this.remittanceInfo || {}, params)
                })
            }
        },
        created () {
            this.getPageParams()
        }
    }
</script>

<style lang="scss" scoped>
    .header-btn-txt{
        margin-right: px2rem(-22);
        font-size: px2rem(28);
        font-family:PingFangSC-Regular;
        line-height: px2rem(40);
        @include font-color(#333333)
    }
    .rec-container {
        padding: px2rem(20) 0;
        .register-list {
            .item {
                display: flex;
                align-items: center;
                min-height: px2rem(120);
                &:last-child {
                    .hasMoreCard .info-wrap, .hasOneCard .info-wrap{
                        border-bottom: none;
                    }
                }
                .hasMoreCard, .hasOneCard {
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    width: 100%;
                    min-height: px2rem(120);
                    .info-wrap {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        height: px2rem(120);
                        width: 100%;
                        border-bottom: 1px solid #DBDBDB;
                        padding:0 px2rem(28);
                        box-sizing: border-box;
                        background-color: #ffffff;
                        position: relative;
                        z-index: 2;
                        .ui-symbol{
                            width: px2rem(78);
                            height: px2rem(78);
                            margin-left: px2rem(-10);
                        }
                    }
                    .left {
                        width: px2rem(58);
                        margin-right: px2rem(20);
                        flex: none;
                    }
                    .middle {
                        flex: 1;
                        display: flex;
                        flex-direction: column;
                        .top {
                            font-size: px2rem(32);
                            line-height: px2rem(44);
                            color: $font-black;
                        }
                        .bottom {
                            margin-top: px2rem(2);
                            font-size: px2rem(24);
                            line-height: px2rem(34);
                            color: #9B9B9B;
                            .bank-name {
                                font-family: PingFangSC-Regular;
                            }
                            .bank-no {
                                font-family: AppleSystemUIFont;
                            }
                        }
                    }
                    .right {
                        flex: none;
                        display: flex;
                        align-items: center;
                        height: 100%;
                        .card-count {
                            color: #F28300;
                            font-size: px2rem(28);
                            height: px2rem(40);
                            line-height: px2rem(40);
                            font-family: PingFangSC-Regular;
                            margin-right: px2rem(24);
                        }
                        .icon-wrap {
                            height: 100%;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            .icon{
                                display: block;
                                color: #9B9B9B;
                                transition: all .3s;
                                &.active{
                                    transform: rotate(180deg);
                                    -ms-transform: rotate(180deg); /* IE 9 */
                                    -moz-transform: rotate(180deg); /* Firefox */
                                    -webkit-transform: rotate(180deg); /* Safari 和 Chrome */
                                    -o-transform: rotate(180deg); /* Opera */
                                }
                            }
                        }
                    }
                }
                .hasMoreCard{
                    overflow: hidden;
                    .left .ui-symbol{
                        margin-left: 0;
                        width: px2rem(60);
                        height: px2rem(60);
                    }
                }
                .card-list {
                    padding: px2rem(20) px2rem(28);
                    background-color: #F0F0F0;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    width: 100%;
                    box-sizing: border-box;
                    margin-top: -49.5%;
                    position: relative;
                    z-index: 1;
                    transition: all .3s;
                    opacity: .5;
                    &.active{
                        margin-top: 0;
                        opacity: 1;
                    }
                    .card-item {
                        width: 100%;
                        background: url("./images/card-bg.png") no-repeat center;
                        background-size: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        color: #ffffff;
                        height: px2rem(90);
                        padding: 0 px2rem(20);
                        margin:0 px2rem(28) px2rem(20) px2rem(28);
                        box-sizing: border-box;
                        &:last-child{
                            margin-bottom: 0;
                        }
                        .icon-wrap {
                            width: px2rem(46);
                            height: px2rem(46);
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            border-radius: 50%;
                            background-color: #ffffff;
                            overflow: hidden;
                            flex: none;
                            margin-right: px2rem(20);
                            .ui-symbol{
                                width: 0.55rem;
                                height: .55rem;
                            }
                        }
                        .bank-name {
                            font-size: px2rem(28);
                            height: px2rem(36);
                            line-height: px2rem(36);
                            font-family: PingFangSC-Regular;
                            flex: 1;
                        }
                        .card-no {
                            font-size: px2rem(32);
                            height: px2rem(36);
                            line-height: px2rem(36);
                            flex: none;
                        }
                    }
                }
            }
        }
    }
</style>



